import { Injectable } from '@angular/core';
import { CanActivate, Router } from '@angular/router';
import { AlertifyService } from '../_services/alertify.service';
import { AuthService } from '../_services/auth.service';

// composant permettant de contrler et restreindre les accs aux routes
@Injectable({
  providedIn: 'root'
})
export class AuthGuard implements CanActivate {
  // dans le constructeur on cre des instances des classes  utiliser pour contrler l'user
  constructor(
    private authService: AuthService,
    private router: Router,
    private alertify: AlertifyService
  ){

  }
          // ********** appel de base avec ng g ruard auth */
  // canActivate(
  //   next: ActivatedRouteSnapshot,
  //   state: RouterStateSnapshot): Observable<boolean | UrlTree> | Promise<boolean | UrlTree> | boolean | UrlTree {
          // on n'a besoin que de savoir si l'user est connect donc un boolean
  canActivate(): boolean{
    // logg ? ok
    if (this.authService.loggedIn()){
      return true;
    }
    // non logg message erreur
    this.alertify.error('dgage de l gros pd');
    // redirection vers accueil
    this.router.navigate(['/home']);
    return false;
  }
}
